<template>
    <div class='set'>
        <div class='title'>个人设置</div>
        <div class='tab' @click='handleClick'>
            <div :class="{'fir': person ? true : false, 'sec': !person ? true : false}" id='fir'>个人中心</div>
            <div :class="{'fir': password ? true : false, 'sec': !password ? true : false, pass: true}" id='sec'>修改密码</div>
        </div>
        <personInfo v-if='person'></personInfo>
        <password v-if='password' v-on:updatePassword="updatePassword"></password>
    </div>
</template>
<script>
import personInfo from '@/components/personInfo'
import password from '@/components/modifypassword'
export default {
    data () {
        return {
            activeName: 'first',
            password: false,
            person: true
        }
    },
    components: {
        personInfo: personInfo,
        password: password
    },
    methods: {
        handleClick (event) {
            let index = event.target.id
            if (index === 'fir') {
                this.person = true
                this.password = false
            } else if (index === 'sec') {
                this.password = true
                this.person = false
            }
        },
        updatePassword () {
            this.person = true
            this.password = false
        }
    }
}
</script>
<style lang="scss" scoped>
.set{
    .title{
        font-family: PingFangSC-Semibold;
        font-size: .24rem;
        color: #263238;
        letter-spacing: 0;
        line-height: .24rem;
        margin-bottom: .4rem;
    }
    .tab{
        display: flex;
        margin-bottom: .2rem;
        div{
            width: 1.4rem;
            height: .36rem;
            line-height: .36rem;
            font-family: PingFangSC-Medium;
            font-size: .16rem;
            color: #FFFFFF;
            background: #20C6BA;
            border-radius: 4px 0px 0px 4px;
            text-align: center;
            cursor: pointer;
        }
        .fir{
            background: #20C6BA;
        }
        .sec{
            background: #fff;
            color: rgba(0,0,0,0.65);
        }
        .pass{
            border-radius: 0px 4px 4px 0px;
        }
    }
}
</style>





