<template>
    <div class='info'>
        <div class='btnlist' v-if="personInfo.userType != 1">
            <div class='edit' @click='editInfo()' v-if='!edit'><img src='../assets/images/btn_edit.svg'/><span>编辑</span></div>
             <div v-if='edit' class='edit cancel' @click='cancel()'>取消</div>
             <div v-if='edit' class='edit sumbit' @click="submit()">完成</div>
        </div>
        <div class='content' v-if='!edit'>
            <div class='left'>
               	 个人资料
            </div>
            <div class='rig'>
            	<div class='num'>
                    <div class='text'>用户身份：</div>
                    <span>{{personInfo.userTypeName}}</span>
                </div>
                <div class='num'>
                    <div class='text'>性别：</div>
                    <span>{{personInfo.genderName}}</span>
                </div>
            	<div class='num'>
                    <div class='text'>用户名：</div>
                    <span>{{personInfo.name}}</span>
                </div>
                <div class='num' v-if="personInfo.userNo">
                    <div class='text'>编号：</div>
                    <span>{{personInfo.userNo}}</span>
                </div>
                <div class='num'>
                    <div class='text'>身份证号：</div>
                    <span>{{personInfo.idCard}}</span>
                </div>
                <div class='num'>
                    <div class='text'>手机号：</div>
                    <span>{{personInfo.phone}}</span>
                </div>
                <div class='num'>
                    <div class='text'>邮箱：</div>
                    <span>{{personInfo.email}}</span>
                </div>
                <div class='num' v-if="personInfo.userType != 1">
                    <div class='text'>专业：</div>
                    <span>{{personInfo.professionalName}}</span>
                </div>
                <div class='num' v-if="personInfo.teacherRankName">
                    <div class='text'>级别：</div>
                    <span>{{personInfo.teacherRankName}}</span>
                </div>
                <div class='num' v-if="personInfo.shouldScore">
                    <div class='text'>应修学分：</div>
                    <span>{{personInfo.shouldScore}}</span>
                </div>
                <div class='num' v-if="personInfo.realScore">
                    <div class='text'>时修学分：</div>
                    <span>{{personInfo.realScore}}</span>
                </div>
            </div>
        </div>
        <div class='editInfo' v-if='edit'>
            <el-form label-width="80px"  ref="form">
                <el-form-item label="用户身份：">
                	<div>{{personInfo.userTypeName}}</div>
                </el-form-item>
                <el-form-item label="性别：" class='sel'>
                    <div>{{personInfo.genderName}}</div>
                </el-form-item>
                <el-form-item label="用户名："  class='sel'>
                    <div>{{personInfo.name}}</div>
                </el-form-item>
                <el-form-item label="编号："  class='sel' v-if="personInfo.userNo">
                    <div>{{personInfo.userNo}}</div>
                </el-form-item>
                <el-form-item label="身份证号："  class='sel'>
                    <div>{{personInfo.idCard}}</div>
                </el-form-item>
                <el-form-item label="手机号：">
                    <el-input v-model="personInfo.phone"></el-input>
                </el-form-item>
                <el-form-item label="邮箱：" class='sel'>
                    <el-input v-model="personInfo.email"></el-input>
                </el-form-item>
                <el-form-item label="专业：" class='sel' v-if="personInfo.professionalName && personInfo.userType != 1">
                    <div>{{personInfo.professionalName}}</div>
                </el-form-item>
                <el-form-item label="级别：" v-if="personInfo.teacherRankName">
                    <div>{{personInfo.teacherRankName}}</div>
                </el-form-item>
                <el-form-item label="应修学分：" class='sel' v-if="personInfo.shouldScore">
                    <div>{{personInfo.shouldScore}}</div>
                </el-form-item>
                <el-form-item label="实修学分：" class='sel' v-if="personInfo.realScore">
                    <div>{{personInfo.realScore}}</div>
                </el-form-item>
            </el-form>

        </div>
    </div>
</template>
<script>
import {modifyInfo} from '@/services/service'
import {isApiResponseOk, getSessionStorage} from '../utils/com.js'
export default {
    data () {
        return {
            edit: false,
            personInfo: {},
            isShow: false
        }
    },
    created () {
        this.initInfo()
    },
    methods: {
        cancel () {
            this.edit = false
            this.initInfo()
        },
        submit () {
        	let that = this
        	let data = {
        		phone: this.personInfo.phone,
        		email: this.personInfo.email
        	}
        	modifyInfo(data).then(function (res){
        		if (res.code == 0) {
	            	that.$message({
	                    showClose: true,
	                    message: '修改成功',
	                    type: 'success'
	                })
	            	that.edit = false
	            }   else  {
	                that.$message({
		                showClose: true,
		                message: res.message,
		                type: 'warning'
	                })
	            }  
        	})
        },
        // 编辑信息
        editInfo () {
            this.edit = true
        },
        initInfo () {
        	this.personInfo = getSessionStorage('userInfo')
        }
    }
}
</script>
<style lang="scss" scoped>
</style>
<style>
	.info .editInfo .el-form .el-form-item{
		height: 42px!important;
	}
	.info .editInfo{
		margin-top: 0.2rem;
	}
	.info .editInfo .el-form .sel .el-form-item__label:after{
		content: ''!important;
	}
	.info .editInfo .el-form .el-form-item .el-form-item__content div{
		text-align: right;
		padding-right: 0.2rem;
	}
	.info .el-input__inner{
		padding: 0!important;
	}
	.info .editInfo .el-form .el-form-item{
		width: 35%;
	}
</style>





