<template>
    <div class='passinfo'>
        <div class='editInfo'>
            <el-form label-width="80px" :model="form" :rules="rules" ref="form">
                <el-form-item label="新密码：" prop="password">
                    <el-input v-model="form.password" type='password' placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="再次输入密码：" prop="confirm_password">
                    <el-input v-model="form.confirm_password" type='password' placeholder="请输入"></el-input>
                </el-form-item>
            </el-form>
            <div class='btn'>
                <div class='cancel' @click='cancel'>取消</div>
                <div class='submit' @click="submit('form')">保存</div>
            </div>
        </div>
    </div>
</template>
<script>
import {modifyPwd} from '@/services/service'
import {isApiResponseOk, getSessionStorage} from '@/utils/com'
export default {
    data () {
        return {
            form: {
                confirm_password: '',
                password: ''
            },
            rules: {
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ],
                confirm_password: [
                    { required: true, message: '请确认密码', trigger: 'blur' }
                ]
            },
            text: '获取验证码',
            hasGet: false,
            time: 0,
            noMobile: false,
            forgetKey: ''
        }
    },
    methods: {
        // 取消
        cancel () {
            this.$emit('updatePassword', false)
        },
        submit(formName) {
        	let that = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                	if (this.form.password != this.form.confirm_password) {
        				this.$message({
        					showClose: true,
        					message: '两次密码不一致',
        					type: 'warning'
        				})
        			}
                	let data = {
                		password: this.form.password
                	}
                	modifyPwd(data).then(function (res){
                		if (res.code == 0) {
			            	that.$message({
			                    showClose: true,
			                    message: '修改成功',
			                    type: 'success'
			                })
			            	that.edit = false
			            }   else  {
			                that.$message({
				                showClose: true,
				                message: res.message,
				                type: 'warning'
			                })
			            }  
                	})
                } else {

                }
            })
        },
        // 编辑信息
        editInfo () {
            this.edit = true
        },
    },
    created () {
    	
    }
}
</script>
<style lang="scss">
.passinfo{
    background: #fff;
    .btnlist{
        height: .6rem;
        display: flex;
        justify-content: flex-end;
        background: #fff;
        align-items: center;
        font-family: PingFangSC-Medium;
        font-size: .16rem;
        letter-spacing: 0;
        padding: .2rem;
        border-bottom: 1px solid #EAECEF;
        .edit{
            width: 1rem;
            height: .36rem;
            line-height: .36rem;
            border: 1px solid #E9ECEF;
            border-radius: .04rem;
            text-align: center;
            color: #20C6BA;
            img{
                margin-right: .1rem;
            }
        }
        .sumbit{
            background-image: linear-gradient(138deg, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
            border-radius: .04rem;
            margin-left: .12rem;
            color: #fff;
        }
        .cancel{
            color: #8597a1;
        }
    }
    .content{
        display: flex;
        align-items: center;
        background: #fff;
        padding: .28rem .6rem;
        .left{
            margin-right: .4rem;
            img{
                width: .6rem;
                height: .6rem;
                border-radius: 60%;
            }
        }
        .rig{
            div{
                font-family: PingFangSC-Regular;
                font-size: .14rem;
                color: #8597A1;
                letter-spacing: 0;
                line-height: .14rem;
                display: flex;
                span{
                    color: #263238;
                    margin-left: .3rem;
                }
            }
            .num{
                margin-bottom: .2rem;
            }
        }
    }
    .editInfo{
        background: #fff;
        padding: .5rem;
        .el-form{
            .el-form-item{
                background: #FBFBFC;
                border: 1px solid #EAECEF;
                display: flex;
                width: 4.5rem;
                margin-bottom: .12rem;
                height: .5rem;
                .el-form-item__content{
                    width: 100%;
                    line-height: .5rem;
                    .el-input{
                        input{
                            border: none;
                            background: #FBFBFC;
                            text-align: right;
                        }
                    }
                    .el-form-item__error{
                        right: 0;
                        text-align: right;
                    }
                }
                .el-form-item__label{
                    white-space: nowrap;
                    text-align: left;
                    padding-left: .15rem;
                    line-height: .5rem;
                    &:after{
                        content: '*';
                        color: #f56c6c;
                        margin-left: 5px;
                    }
                    &:before{
                        content: '';
                    }
                }
            }
            .picture{
                background: #fff;
                border: none;
                text-align: center;
                margin-top: .3rem;
                margin-left: 0rem;
                width: 100%;
                justify-content: center;
                img{
                    vertical-align: middle;
                }
            }
            .tips{
                font-family: PingFangSC-Medium;
                font-size: .12rem;
                color: #C0C4CC;
                letter-spacing: 0;
                line-height: .12rem;
                text-align: right;
                width: 4.5rem;
                margin-bottom: .1rem;
            }
            .veri{
                .el-form-item__content{
                    display: flex;
                    align-items: center;
                    .el-form-item__error{
                        right: 0;
                        text-align: right;
                    }
                }
            }
        }
    }
    .btn{
        display: flex;
        justify-content: center;
        margin-top: .26rem;
        div{
            width: 1.22rem;
            height: .44rem;
            line-height: .44rem;
            font-family: PingFangSC-Medium;
            font-size: .16rem;
            color: #FFFFFF;
            letter-spacing: 0;
            border-radius: 2px;
            text-align: center;
            cursor: pointer;
        }
        .cancel{
            background: #C0C4CC;
            margin-right: .2rem;
        }
        .submit{
            background: #20C6BA;
        }
    }
    .noMobile{
        color: #8597A1;
        border: 1px solid rgba(133, 151, 161, 0.3);
        font-size: .10rem;
        padding: .11rem .12rem !important;
        margin-left: .1rem;
        font-weight: bold;
        background: none;
        border-radius: 0px !important;
        height: .34rem;
        margin-right: .1rem;
    }
    .code{
        border: 1px solid rgba(32,198,186,0.3);
        color: #20c6ba;
    }
}
</style>
<style>
	.passinfo .editInfo .el-form .el-form-item{
		height: 42px!important;
		margin-bottom: 24px;
	}
</style>





